import React,{Component} from 'react';
import logo from './logo.svg';
import './App.css';
import Clockcurrent from './Clockcurrent';
import Clock from './Clock'

class App extends Component {
  constructor(props){
    super(props);
    this.state={
      deadline: 'August 15, 2019',
      newDeadline:''
    }
  }
  changeDeadLine(){
    console.log('state',this.state);
    this.setState({deadline:this.state.newDeadline});
    //this.setState({deadline: 'November 25, 2019'})
  }
  render() {
    return (
    <div className="App">
      <header className="App-header">
      <Clockcurrent />
      <small>Made with ♥ by <a href="https://github.com/tamaghno" target="_blank">Tamaghno Chaudhuri</a></small>
      <img src='data:image/jpeg;base64,'/>

        <img src={logo} className="App-logo" alt="logo" />
        
        <br />
        <br />
        <input placeholder='new date for countdown' 
              //onChange={event=>console.log('event',event.target.value)}/>
              onChange={event=>this.setState({newDeadline:event.target.value} )}/>

        <button onClick={()=>this.changeDeadLine()}> submit </button>
        <div>CountDown to Sacred Games Season 02 : {this.state.deadline}</div>
        <h2>BHAGWAN KO MANTE HO ?</h2>
        <div>
       <Clock 
       deadline={this.state.deadline}/>
        </div>
         
      </header>
    </div>
    );
  }
}

export default App;
